body {
background:black ;
font-size: 20px;
color: white;
}

.logo{
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
}

.input-text {
  display: inline;
  margin: 0;
  border: none;
  border-radius: 0.4rem;
  padding: 10px;
}

.list ul li button {
  position: absolute;
  right: 0%;
  background: #f80d09;
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  margin-left: 10px;
  margin-right: 10px;
  padding: 10px;
  border-radius: 0.4rem;
  display: inline-block;
  border: none;
}

.b-t {
  position: absolute;
  background: #f80d09;
  color: #fff !important;
  text-transform: uppercase;
  margin-left: 15px;
  margin-right: 15px;
 
  text-decoration: none;
  padding: 10px;
  border-radius: 0.4rem;
  display: inline-block;
  border: none;
}
.add-btn {
  
  text-transform: uppercase;
  text-decoration: none;
  background:rgb(17, 230, 17);
  color:white;
  margin: 20px;
  padding: 10px;
  border-radius: 0.4rem;
  display: inline-block;
  border: none;
}

.app-title {
  text-align: center;
  color: #fff;
  font-size: 70px;
}
.app-tit {
  text-align: center;
  color: #fff;
  font-size: 20px;
}
ul {
  /* remove default padding and margin from ul*/
  margin: 0px;
  padding: 0px;
}
.list ul li {
  display: block;
  width: 100%;
  text-decoration: none;
  color: #000000;
  background-color: #ffffff;
  line-height: 30px;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: #cccccc;
  padding: 10px;
  position: relative;
}

.container {
  color: #fff;
  max-width: 500px;
  margin: auto;
}